.enviado-correctamente {
  display: flex;
  flex-direction: column;
  align-items: center;

  padding-top: 1rem;
}

.registry-header__logo {
  width: 2.625rem;
  margin-bottom: 0.5rem;
}

@media (min-width: 800px) {
  .registry-header__logo {
    width: 5.75rem;
    margin-bottom: 1rem;
  }

  .registry-header__title {
    margin-bottom: 2rem;
  }
}



.registry-header__logo {
  width: 2.625rem;
  margin-bottom: 5rem;
}


.registry-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.registry-card__icon-complete {
  display: block;
  width: 3.5rem;
  height: 3.5rem;

  margin-bottom: 1rem;

  background-size: cover;
  background-repeat: no-repeat;
}

.registry-card__title {
  max-width: 20rem;
}

@media (min-width: 800px) {
  .registry-header__logo {
    width: 5.75rem;
    margin-bottom: 1rem;
  }

  .registry-header__title {
    margin-bottom: 2rem;
  }
}
