  display: block;
  border: none;
  border-radius: 1rem;
}
.boton-amarillo:hover {
  background-color: #af6907;
  cursor: pointer;
}

.boton-verde {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
  border-radius: 1rem;
}
.boton-verde:hover {
  background-color: #507e00;
  cursor: pointer;
}

.boton-amarillo-inline {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
  border-radius: 1rem;
}
.boton-amarillo-inline:hover {
  background-color: #af6907;
  cursor: pointer;
}

.boton-rojo-block {
  border-radius: 1rem;
  background-color: #ff1f1f;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.boton-rojo-block:hover {
  background-color: #eb0000;
  cursor: pointer;
}

.boton-verde-block {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.boton-verde-block:hover {
  background-color: #507e00;
  cursor: pointer;
}

.boton-tabla {
  height: 5rem;
  margin: 1.5rem 0 1rem auto;
  width: 20rem;
}

.button {
  cursor: pointer;
  display: block;
  background-color: #050a30;
  border-radius: 7px;
  border: 1px solid rgba(255, 255, 255, 0.3);
  width: 100%;
  margin-top: 1rem;
  max-width: 20rem;
  padding: 1.125rem;
  box-sizing: border-box;
  color: rgba(255, 255, 255, 0.5);
  font-size: 22px;
  align-self: center;
  text-align: center;
}

.button:hover {
  background-color: #050a30;
  border: 1px solid rgba(255, 255, 255, 0.8);
  color: #FFFFFF;
}

.simple-button {
  border: none;
  background-color: unset;
}

.simple-button--add {
  color: #050a30;
}

.simple-button--delete {
  color: #ff1f1f;
}

.simple-button--edit {
  color: #047900;
}

.cartao {
  margin: 0 auto;
  background-color: rgba(168, 100, 93, 0.2);
  border-radius: 10px;
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.3568627451);
  width: 90%;
  max-width: 45rem;
  padding: 1.75rem;
  box-sizing: border-box;
  margin-bottom: 1rem;
}

.card__title {
  font-size: 28px;
  margin-bottom: 1rem;
}

.formulario {
  margin-top: 2rem;
}

.form__label {
  font-size: 19px;
  margin-bottom: 1rem;
}

.input-container {
  font-weight: 300;
  position: relative;
  margin-bottom: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  box-sizing: border-box;
}

.input {
  box-sizing: border-box;
  border-bottom: 1px solid rgba(81, 129, 231, 0.9529411765);
  padding: 1.375rem 0.5rem 0.5rem;
  height: 3.25rem;
  width: 100%;
}

.input::placeholder {
  visibility: hidden;
  color: rgba(0, 0, 0, 0);
}

.input:focus {
  outline: none;
}

.input-label {
  position: absolute;
  top: 1rem;
  left: 0.5rem;
  font-size: 15px;
  transition: all 0.25s;
}

.input:not(:placeholder-shown) + .input-label,
.input:focus + .input-label {
  font-size: 0.875rem;
  top: 0.25rem;
  transition: all 0.25s;
}

.input-container--invalid {
  margin-bottom: 0.5rem;
}

.input-container--invalid .input {
  border: 1px solid #ff1f1f;
  border-radius: 7px;
}

.input-container--invalid .input-label {
  color: #b80b0b;
}

.input-message-error {
  display: none;
}

.input-container--invalid .input-message-error {
  color: #ff1f1f;
  display: block;
  margin-top: 0.5rem;
  padding-left: 0.5rem;
}

.textarea {
  box-sizing: border-box;
  border: 1px solid #FFFFFF;
  padding: 0.5rem;
  border-radius: 7px;
  width: 100%;
  min-height: 3rem;
}

.textarea-container {
  position: relative;
  margin-top: 2rem;
  margin-bottom: 1rem;
  font-weight: 300;
}

.textarea::placeholder {
  visibility: hidden;
}

.textarea:focus {
  outline: none;
}

.textarea-label {
  position: absolute;
  top: 0.5rem;
  left: 0.5rem;
  font-size: 15px;
  transition: all 0.25s;
}

.textarea:not(:placeholder-shown) + .textarea-label,
.textarea:focus + .textarea-label {
  font-size: 0.875rem;
  top: -1.25rem;
  transition: all 0.25s;
}

.textarea-container--invalid {
  margin-bottom: 0.5rem;
}

.textarea-container--invalid .textarea {
  border: 1px solid #ff1f1f;
}

.textarea-container--invalid .textarea-label {
  color: #ff1f1f;
}

.textarea-mensagem-erro {
  display: none;
}

.textarea-container--invalid .textarea-mensagem-erro {
  display: block;
  color: rgba(97, 21, 12, 0.9607843137);
  margin-top: 0.5rem;
  padding-left: 0.5rem;
}

.modal {
  background-color: #FFFFFF;
  border-radius: 10px;
  padding: 1.75rem;
  box-sizing: border-box;
  max-width: 22.5rem;
  position: relative;
}

.modal-container {
  background-color: rgba(77, 77, 77, 0.2);
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  transition: 0.25s;
}

.modal__title {
  font-size: 28px;
  color: #ff1f1f;
  margin-bottom: 1rem;
}

.modal__button {
  font-size: 28px;
}

.modal__texto {
  margin-bottom: 3.875rem;
}

.modal__button--confirm {
  color: #ff1f1f;
  margin-right: 2.25rem;
}

.modal__button-container {
  display: flex;
}

.modal__close {
  position: absolute;
  display: block;
  height: 1rem;
  width: 1rem;
  top: 1.75rem;
  right: 1.75rem;
}

.modal--closed {
  left: -100vw;
  transition: 0.25s;
}

.no-webp .inicio {
  background-image: url(../img/header.jpg);
}

.webp .inicio {
  background-image: url(../img/header.webp);
}

.header {
  max-width: 1350px;
  margin: auto;
  background: linear-gradient(to right, #050a30 30%, rgba(97, 21, 12, 0.9607843137));
  color: #FFFFFF;
  padding: 1rem 0 3rem 0;
}

.logo-titulo .resaltado {
  color: #FFFFFF;
  font-weight: 700;
  font-family: "Roboto", sans-serif;
  font-weight: 700;
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  color: #FFFFFF;
}
.contenido-header h1 {
  font-weight: 700;
  text-align: center;
  font-size: 2.5rem;
}
@media (min-width: 768px) {
  .contenido-header h1 {
    font-size: 3rem;
    text-align: left;
  }
}
@media (min-width: 768px) {
  .contenido-header .barra {
    padding: 10px;
    margin: 0;
  }
}
.contenido-header .logo {
  width: 20rem;
  margin-bottom: 15px;
  background-repeat: no-repeat;
}
.contenido-header .imagen-logo {
  width: 30rem;
  margin: 0;
}
.contenido-header .navegacion a:hover {
  color: #E08709;
}

.footer {
  background: linear-gradient(to right, #050a30 30%, rgba(97, 21, 12, 0.9607843137));
  text-align: center;
  padding: 1rem 0;
  margin: 0 auto;
  width: 100%;
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
  height: auto;
}
@media (min-width: 480px) {
  .footer .navegacion {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
  }
  .footer .navegacion a {
    margin-right: 2rem;
  }
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}

.derecha {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media (min-width: 900px) {
  .derecha {
    align-items: flex-end;
  }
}
.derecha .dark-mode-boton {
  width: 3rem;
  margin: 3rem 0 2rem 0;
  filter: invert(100%);
}
.derecha .dark-mode-boton:hover {
  cursor: pointer;
}
@media (min-width: 900px) {
  .derecha .dark-mode-boton {
    margin-top: 0;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 2rem;
}
@media (min-width: 900px) {
  .barra {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.1s linear, height 0.3s linear;
}
@media (min-width: 900px) {
  .navegacion {
    opacity: 1;
    visibility: visible;
    display: flex;
    align-items: center;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
@media (min-width: 900px) {
  .navegacion a {
    margin-right: 2rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}
@media (min-width: 900px) {
  .mostrar {
    flex-direction: row;
  }
}

@media (min-width: 900px) {
  .mobile-menu {
    display: none;
